*{
    -webkit-user-select: none !important;
       -moz-user-select: none !important;
        -ms-user-select: none !important;
            user-select: none !important;
}
.home-page{
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    overflow: hidden;
}
.home-pane:hover{
    -webkit-transform: scale(1.01);
        -ms-transform: scale(1.01);
            transform: scale(1.01);
}
.title-bar{
    max-height: 35%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding-top: 80px;
}
.title-bar img{
    max-height: 200px;
    width: auto;
    margin-left: -5%;
}
.title-bar .title{
    color: rgb(172, 58, 58);
}
.login-form{
    width:  50%;
    margin: 0 10px;
}
.login-form-content{
    padding: 50px !important;
    padding-top: 0 !important;
}
.username, .password, .login {
    margin-top: 10px !important;
}
.signup-line{
    padding-top: 10px !important;
}
.login-form-header{
    padding: 50px !important;
    padding-bottom: 0 !important;
}
.home-page-content{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 80%;
    margin-top: 40px;
    max-height: 55%;
}


